  async UpdateBooking(data) {
    const modal = await this.modalController.create({
      component: ModalPage,
      cssClass: 'my-modal-class',
      componentProps: {
        'UpdateBooking': data,
        'Category': this.Category,
      }
    });
    console.log('modal')
    return await modal.present();
  }

  

  async seeDeatils(data) {
    const modal = await this.modalController.create({
      component: ModalPage,
      cssClass: 'my-modal-class',
      componentProps: {
        'bookingDetails' : data,
        'Category': "viewbookingdetails",
      }
    });
    return await modal.present();
  }

